.underline--animation span,
.underline--animation p {
  position: relative;
  padding-bottom: 5px; }
  .underline--animation a:before,
  .underline--animation span:before,
  .underline--animation p:before {
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    position: absolute;
    left: 0;
    bottom: -1px;
    /* this is to match where the border is */
    transform-origin: left;
    transform: scale(0);
    transition: 0.25s linear; }
  .underline--animation a:hover:before, .underline--animation a.active:before,
  .underline--animation span:hover:before,
  .underline--animation span.active:before,
  .underline--animation p:hover:before,
  .underline--animation p.active:before {
    transition: transform 0.25s linear;
    transform: scale(1); }

.underline--transition span {
  cursor: pointer; }

.underline--transition:hover a,
.underline--transition:hover span {
  opacity: 1 !important; }

.underline--transition a,
.underline--transition span {
  font-family: "Roboto", sans-serif; }

