  .underline--transition a:before,
  .underline--transition span:before,
  .underline--transition p:before {
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    background-color: #f5f5f5;
    position: absolute;
    left: 0;
    bottom: -1px;
    /* this is to match where the border is */
    transform-origin: left;
    transform: scale(0);
    transition: 0.25s linear; }
  .underline--transition a:hover:before,
  .underline--transition span:hover:before,
  .underline--transition p:hover:before {
    transition: transform 0.25s linear;
    transform: scale(1); }

